.App {
  display: flex;
  min-height: 100vh;
}
.Landing {
  border: cyan 5px solid;
  width: calc(100vw - 4em);
  height: 100%;
}

.Title {
  background: linear-gradient(to bottom, #0bffffd3,rgba(131, 130, 130, 0.897),rgb(0, 0, 0)),
  url('../assets/images/code-background.jpg');
  background-position: bottom right;
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media (min-width: 600px) {
  .Title {
    padding-top: 5.5em;
  }
  .Name {
    font-size: 6em;
  }
  .Job {
    font-size: 6em;
    font-family: 'Festive', cursive;
  }
}

.TitleInnerWrapper {
  margin: 0 0 0 2em;
  margin: 1em;
  width: fit-content;
  background: linear-gradient(to right, rgb(122, 250, 164), rgb(237, 248, 248), rgb(230, 230, 107));
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
  font-family: 'Cabin Sketch', cursive;
  animation: Title ease-in-out 1s  infinite;
}
@keyframes Title {
  to {
    background: linear-gradient(to right, rgb(230, 230, 107), rgb(122, 250, 164), rgb(237, 248, 248));
    background-clip: text;
    -webkit-background-clip: text;
  }
}
.Name {
  font-size: 4.5em;
}
.Job {
  /* font-size: 7vw; */
  font-family: 'Festive', cursive;
}
.Job span {
  color: rgba(255, 255, 255, 0.363);
  font-weight: 100;
  line-height: 3em;
}
.Job span:nth-child(3) {
  margin-left: 10px;
  margin-right: 10px;
}
.ImageWrapper {
  background: rgba(255, 255, 255, 0.8);
  border-radius: 50%;
  box-shadow: inset 0px 0px 30px rgb(132, 238, 238);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 250px;
  height: 250px;
  margin: 0 auto 20px;
}
.ImageWrapper img {
  border-radius: 20%;
  width: 75%;
  height: 75%;
}


